.name {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.1rem;
    margin: 1rem 0;
    background-color: #4b4b4b;
  }
  
  .name h1 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
    color: #3a3a3a;
    font-size: 3rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
    padding: 1.5rem;
    border-radius: 23px;
    font-family: 'Mirza', cursive;
  }
  
  .name h2 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    flex-flow: column-reverse;
    justify-content: center;
    flex: 1;
    color: #3a3a3a;
    font-size: 1.8rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
    
  }
  
  .name h3 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
    flex-flow: column-reverse;
    justify-content: flex-end;
    flex: 1;
    color: #3a3a3a;
    font-size: 20px;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  .name p {
      font-size: 32px;
      color:white;
  }
  
  