.App {
  padding: 0.4rem;
  background-color: rgb(0, 0, 0);
  width: 100rem;
  max-width: 99%;
}

.App h1 {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-flow: column-reverse;
  justify-content: flex-start;
  font-size: 72px;
  margin-top: 0;
  margin-bottom: 0.5px;
}
.App h2 {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-flow: column-reverse;
  justify-content: flex-start;
  margin-top: 0;
}

.App h3 {
  color: white;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  flex-flow: column-reverse;
  justify-content: flex-start;
  margin-top: 0;
}